@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}


.cover-container {
    width: 100%;
    object-fit: contain;
    /* box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6); */
    display: flex;
    background: linear-gradient(180deg,#0f2027 60%, #203a43, #2c5364);
    height: 100vh;
}

.cover-video {
    object-fit: cover;
    width: 33%;
    overflow: hidden;
    position: absolute;
    z-index: 1;
    height: 100vh;
}

.cover-title {
    color: #B2BABB; 
    padding-top: 4rem;
    padding-left: 5rem;
    width: 40%;
    z-index: 2;
}


.cover-servicios {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    width: 100%;
}

.cover-servicios_description{
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.cover-servicioTecnico {
    color: #B2BABB;
    padding-top: 3.8rem;
}

.cover-image {
    width: 100%;
    opacity: 0.4;
    border: #203a43 solid 1px; 
}

.cover-servicios_titles {
    color: white;
    position: absolute;
}


@media only screen and (max-width: 960px){
    .cover-container {
        flex-direction: column;
        height: 250vh;
    }
    .cover-title {
        padding-top: 11rem;
        margin-left: 2rem;
    }
    .cover-video {
        width: 100%;
        height: 72vh;
        z-index: 0;
    }
    .cover-servicios {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-bottom: 10rem;
    }

    .cover-servicios_description{
        position: absolute;
        width: 100%;
    }
    .cover-image {
        overflow: hidden;
        opacity: 0.2;
        border-radius: 10px;
        border: #203a43 solid 1px;
        object-fit: inherit;
        width: inherit;
        height: 28vh;
    }
    .cover-servicios_titles{
        padding-top: 1rem;
        width: 15rem;
    }
    .cover-servicios_titles h3 {
        font-size: 1rem;
    }
    .cover-servicios_titles h5 {
        font-size: 1rem;
    }
    .cover-servicioTecnico {
        padding-top: 85rem;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-left: 1.3rem;
        margin-bottom: 3rem;
    }
}
#ServicioTecnico {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-left: 3.5rem;
    padding-top: 3rem;
} 

.ServicioTecnico-title {
    color: #B2BABB;
    padding-bottom: 1rem;
    padding-right: 1rem;
}


@media only screen and (max-width: 960px) {
    .ServicioTecnico-title {
        padding-right: 3.4rem;
    } 
}

#Tramites-container {
  width: 100%;
  height: 100vh;
} 
#Tramites-title {
  color: black;
  padding-top: 5rem;
  text-align: center;
}
#Tramites-cards {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-top: 3rem;
}
.carousel__container {
  margin-top: 1rem;
  padding: 3rem 3rem 2.5rem 3rem;
  overflow: auto;
  width: 100%;
  margin-bottom: 1rem;
  white-space: nowrap;
}
.carousel__container::-webkit-scrollbar {
  height: 1rem;
}
.carousel__container::-webkit-scrollbar-thumb {
  width: 2rem;
  background: #A3E4D7;
  border-radius: 0.5rem;
}
.carousel__container:hover .carousel__container-item {
  opacity: 0.9;
}
.carousel__container:hover .carousel__container-item:hover {
  opacity: 1;
  transform: scale(1.3);
}
.carousel__container-item {
  width: 13rem;
  height: 6rem;
  border: 2px solid #96DBF1;
  border-radius: 25px;
  display: inline-block;
  margin-right: 10px;
  transform-origin: center left;
  transition: 450ms all;
  overflow: hidden;
  cursor: pointer;
}
.carousel__container-item:hover ~ .carousel__container-item{
  transform: translate3d(90px, 0, 0);
}
.carousel__container-item__img {
  width: 13rem;
  height: 6.5rem;
  object-fit: cover;
}
#servicios {
  padding: 2rem;
}
#servicios h1 {
